.headingmembership {
  h5 {
    color: #211b3a;
    font-family: Urbanist;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px; /* 116.667% */
  }
}
.membershipcard {
  border-radius: 12px;
  background: #fff;
  padding: 20px;
  padding-bottom: 60px;
  h4 {
    color: #211b3a;
    font-family: Urbanist;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px; /* 116.667% */
    margin-bottom: 0;
  }
  .badge {
    border-radius: 6px;
    background: #d3ffb3;
    padding: 4px 12px;
    color: #3c9718;
    font-family: Urbanist;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: var(--Line-Heights-10, 20px); /* 166.667% */
    letter-spacing: var(--Letter-Spacing-4, 0.25px);
  }
  hr {
    color: black;
    opacity: 0.14;
  }
}
.details {
  color: rgba(0, 0, 0, 0.78);
  font-family: Urbanist;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px; /* 155.556% */
  p {
    color: #000;
    font-family: Urbanist;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 28px; /* 155.556% */
    margin-bottom: 0;
    min-width: 140px;
  }
}

.pricing-card {
  background: #fff;
  border-radius: 12px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  svg{
    min-width: 20px;
  }
}

.pricing-card h5 {
  color: #000;
text-align: center;
font-family: Urbanist;
font-size: 24px;
font-style: normal;
font-weight: 600;
line-height: 39.943px; /* 166.428% */
letter-spacing: -0.067px;
}
.pricing-price {
  color: #000;
font-family: Urbanist;
font-size: 16px;
font-style: normal;
font-weight: 400;
letter-spacing: -0.067px;
}
.pricing-card ul {
  list-style: none;
  padding: 0;
  margin-bottom: 20px;
  color: rgba(0, 0, 0, 0.78);
font-family: Urbanist;
font-size: 15px;
font-style: normal;
font-weight: 400;
letter-spacing: -0.067px;
}
.pricing-card ul li {
  margin: 12px 0;
}
.btn-custom {
  background-color: #715CC3;
  color: #FFF;
text-align: center;
font-family: Urbanist;
font-size: 14px;
font-style: normal;
font-weight: 500;
width: 100%;
  border-radius: 4px;
}
.btn-custom:hover {
  background-color: #6846db;
  color: #fff;
}
